import React, {Component, Fragment } from 'react';
import ApexCharts from "apexcharts";

class TemperatureWrapper extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTemperature: 'tavg',
            gradient: ['rgba(250,174,50,1)','rgba(250,174,50,0)'],
            gradientLinearity: [0, 0, 0, 150]
        }
    }

    updateSelection(newTemperature, gradient, gradientLinearity) {
        this.setState(state => ({
            selectedTemperature: newTemperature,
            gradient: gradient,
            gradientLinearity: gradientLinearity
        }));
    }

    render() {
        return(
            <div className="card rounded-0">
                <div className="card-body">
                    <div className="row">
                        <div className="col-5">
                            <h5 className="card-title">Yearly Temperature</h5>
                        </div>
                    </div>
                    <div>
                        <Temperature id={this.state.selectedTemperature} data={this.props.data} gradient={this.state.gradient} gradientLinearity={this.state.gradientLinearity}/>
                    </div>
                </div>
            </div>
        );
    }
}

class Temperature extends Component {
    constructor(props) {
        super(props);
        this.state = {
            type: this.props.type,
            id: this.props.id,
        }
    }

    componentDidMount() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
    
    }
    
    componentDidUpdate() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
        
    }

    createGraph() {
        document.querySelector('#apexChartsId').innerHTML = '';

        let measurementUnit = '°C';
        let options = {
            series: [
                {
                    name: "Max",
                    data: [
                        this.props.data.data[0]['tmax'] + measurementUnit, 
                        this.props.data.data[1]['tmax'] + measurementUnit,
                        this.props.data.data[2]['tmax'] + measurementUnit, 
                        this.props.data.data[3]['tmax'] + measurementUnit,
                        this.props.data.data[4]['tmax'] + measurementUnit, 
                        this.props.data.data[5]['tmax'] + measurementUnit, 
                        this.props.data.data[6]['tmax'] + measurementUnit, 
                        this.props.data.data[7]['tmax'] + measurementUnit,
                        this.props.data.data[8]['tmax'] + measurementUnit, 
                        this.props.data.data[9]['tmax'] + measurementUnit, 
                        this.props.data.data[10]['tmax'] + measurementUnit, 
                        this.props.data.data[11]['tmax'] + measurementUnit
                    ]    
                },
                {
                    name: "Average",
                    data: [
                        this.props.data.data[0]['tavg'] + measurementUnit, 
                        this.props.data.data[1]['tavg'] + measurementUnit,
                        this.props.data.data[2]['tavg'] + measurementUnit, 
                        this.props.data.data[3]['tavg'] + measurementUnit,
                        this.props.data.data[4]['tavg'] + measurementUnit, 
                        this.props.data.data[5]['tavg'] + measurementUnit, 
                        this.props.data.data[6]['tavg'] + measurementUnit, 
                        this.props.data.data[7]['tavg'] + measurementUnit,
                        this.props.data.data[8]['tavg'] + measurementUnit, 
                        this.props.data.data[9]['tavg'] + measurementUnit, 
                        this.props.data.data[10]['tavg'] + measurementUnit, 
                        this.props.data.data[11]['tavg'] + measurementUnit
                    ]
                },
                {
                    name: "Min",
                    data: [
                        this.props.data.data[0]['tmin'] + measurementUnit, 
                        this.props.data.data[1]['tmin'] + measurementUnit,
                        this.props.data.data[2]['tmin'] + measurementUnit, 
                        this.props.data.data[3]['tmin'] + measurementUnit,
                        this.props.data.data[4]['tmin'] + measurementUnit, 
                        this.props.data.data[5]['tmin'] + measurementUnit, 
                        this.props.data.data[6]['tmin'] + measurementUnit, 
                        this.props.data.data[7]['tmin'] + measurementUnit,
                        this.props.data.data[8]['tmin'] + measurementUnit, 
                        this.props.data.data[9]['tmin'] + measurementUnit, 
                        this.props.data.data[10]['tmin'] + measurementUnit, 
                        this.props.data.data[11]['tmin'] + measurementUnit
                    ]
                }
            ],
            colors: [ '#e67e7e', '#b5b5b5', '#47aefc'],
            legend: {
                show: false
            },
            chart: {
                height: 220,
                type: 'line',
                zoom: {
                    enabled: false
                },
                toolbar: {
                    show: false
                },
                dropShadow: {
                    enabled: true,
                    enabledOnSeries: undefined,
                    top: 0,
                    left: 10,
                    blur: 6,
                    color: '#000',
                    opacity: 0.15
                }
            },
            dataLabels: {
                enabled: false
            },
            stroke: {
                curve: 'smooth',
                dashArray: [0,8,0]
            },
            grid: {
                row: {
                    colors: ['transparent'], // takes an array which will be repeated on columns
                    opacity: 0.5
                },
            },
            xaxis: {
                categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            }
          };
  
          var chart = new ApexCharts(document.querySelector("#apexChartsId"), options);
          chart.render();
    }

    render() {
        return (
            <Fragment>
                <div id="apexChartsId"></div>  
            </Fragment>
        );
    }
}

export default TemperatureWrapper;