import React, {Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';

import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import am4themes_frozen from "@amcharts/amcharts4/themes/frozen";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

class Map extends Component {

    componentDidMount() {
        this.createMap();
    }

    componentDidUpdate() {
        this.createMap();
    }

    createMap(){
        /* Chart code */
        // Themes begin
        am4core.useTheme(am4themes_frozen);
        am4core.useTheme(am4themes_animated);
        // Themes end

        // Create map instance
        let chart = am4core.create("worldMap", am4maps.MapChart);

        // Set map definition
        chart.geodata = am4geodata_worldLow;

        // Set projection
        chart.projection = new am4maps.projections.Miller();

        // Center on the groups by default
        chart.homeZoomLevel = 5;
        chart.homeGeoPoint = { 
            longitude: parseInt(document.location.pathname.replace("/","").split("&")[2]),
            latitude: parseInt(document.location.pathname.replace("/","").split("&")[1])
        };

        // Create map polygon series
        let polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());

        // Make map load polygon (like country names) data from GeoJSON
        polygonSeries.useGeodata = true;

        // Configure series
        let polygonTemplate = polygonSeries.mapPolygons.template;
        polygonTemplate.tooltipText = "{name}";
        polygonTemplate.polygon.fillOpacity = 0.6;


        // Create hover state and set alternative fill color
        let hs = polygonTemplate.states.create("hover");
        hs.properties.fill = chart.colors.getIndex(0);

        // Add image series
        let imageSeries = chart.series.push(new am4maps.MapImageSeries());
        imageSeries.mapImages.template.propertyFields.longitude = "longitude";
        imageSeries.mapImages.template.propertyFields.latitude = "latitude";
        imageSeries.mapImages.template.tooltipText = "{title}";
        imageSeries.mapImages.template.propertyFields.url = "url";

        let circle = imageSeries.mapImages.template.createChild(am4core.Circle);
        circle.radius = 1;
        circle.propertyFields.fill = "color";

        let circle2 = imageSeries.mapImages.template.createChild(am4core.Circle);
        circle2.radius = 1;
        circle2.propertyFields.fill = "color";

        circle2.events.on("inited", function(event){
            animateBullet(event.target);
        })


        function animateBullet(circle) {
            let animation = circle.animate([{ property: "scale", from: 1, to: 5 }, { property: "opacity", from: 1, to: 0 }], 1000, am4core.ease.circleOut);
            animation.events.on("animationended", function(event){
            animateBullet(event.target.object);
            })
        }

        let colorSet = new am4core.ColorSet();
        
        imageSeries.data = [ {
            "title": decodeURIComponent(document.location.pathname.replace("/Weather/","").split("&")[0]),
            "latitude": parseInt(document.location.pathname.replace("/","").split("&")[1]),
            "longitude": parseInt(document.location.pathname.replace("/","").split("&")[2]),
            "color":colorSet.next()
        }]
        
        //Remove logo
        document.querySelectorAll('[aria-labelledby]')[0].style.display = "none"
            
    };

    render() {
        return (
            <Fragment>
                <div id="worldMap" className="h-90"></div>
            </Fragment>
        );
    }
}

export default withRouter(Map);
