import React from 'react';

function Stats(props) {

    function getDeviation(data, property, months) {
        let average = calculateAverage(data, property, months)
        return (average - 12.7).toFixed(1);
    }

    function getMaximum(response, property) {
        if (typeof response.data === "undefined") return;
        let max = response.data[0][property];
        response.data.forEach( element => {
            if (element[property] > max) max = element[property]; 
        });
        return max;
    }

    function getMinimum(response, property) {
        if (typeof response.data === "undefined") return;
        let min = response.data[0][property];
        response.data.forEach( element => {
            if (element[property] < min) min = element[property]; 
        });
        return min;
    }

    function calculateAverage(response, property, months) {
        if (typeof response.data === "undefined") return;
        let total = 0;
        response.data.forEach(element => {
            total += element[property]
        });
        return((total/months).toFixed(1));

    }

    function computeData(data, property, months) {
        let output;
        switch (property) {
            case "deviation":
                output = getDeviation(data, 'tavg', months);
                break;
            case "tavg":
                output = calculateAverage(data, property, months);
                break;
            case "tmax":
                output = getMaximum(data, property);
                break;
            case "tmin":
                output = getMinimum(data, property);
                break;
            default:
                break;
        }
        return output;
    }

    function getTextColor(data, property, months) {
        let classColor;
        switch (property) {
            case "deviation":
                classColor = getDeviation(data, 'tavg', months) > 0 ? 'text-warm' : 'text-cold';
                break;
            case "tavg":
                classColor = 'text-muted';
                break;
            case "tmax":
                classColor = 'text-warm';
                break;
            case "tmin":
                classColor = 'text-cold';
                break;
            default:
                break;
        }
        return classColor;
    }

    return(
        <div className="card rounded-0 h-100">
            <div className="card-body">
                <h5 className="card-title one-line">{props.title}</h5>
                <br></br>
                <div className={"card-text " + getTextColor(props.data, props.value, 12)}>
                    <h6>
                      {computeData(props.data, props.value, 12) + "°C"}  
                    </h6>
                </div>
            </div>
        </div>
    );
}

export default Stats;