import React, {Component, Fragment } from 'react';
import SearchBox from './SearchBox';
import Precipitation from './Precipitation';
import TemperatureWrapper from './Temperature';
import Map from "./Map";
import { withRouter } from "react-router-dom";
import Stats from './Stats';

class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            response: {},
            selectedTemperature: 'tavg',
            city: decodeURIComponent(document.location.pathname.replace("/Weather/","").split("&")[0])
        }
    }

    componentDidMount() {
        let lat = document.location.pathname.replace("/","").split("&")[1];
        let long = document.location.pathname.replace("/","").split("&")[2];

        if (document.getElementById('loader-overlay') !== null) document.getElementById('loader-overlay').style.display = "block";
        fetch("https://nodejs-295719.ew.r.appspot.com/stats?lat="+lat+"&long="+long)
        .then( response => response.json())
        .then( (response) => {
            console.log(response);
            this.setState(state => ({response: response, city: decodeURIComponent(document.location.pathname.replace("/Weather/","").split("&")[0])}));
            if (document.getElementById('loader-overlay') !== null) document.getElementById('loader-overlay').style.display = "none";
        });
    }

    componentDidUpdate() {
        if (this.state.city !== decodeURIComponent(document.location.pathname.replace("/Weather/","").split("&")[0])) {
            let lat = document.location.pathname.replace("/","").split("&")[1];
            let long = document.location.pathname.replace("/","").split("&")[2];
            
            if (document.getElementById('loader-overlay') !== null) document.getElementById('loader-overlay').style.display = "block";
            fetch("https://nodejs-295719.ew.r.appspot.com/stats?lat="+lat+"&long="+long)
            .then( response => response.json())
            .then( (response) => {
                this.setState(state => ({response: response, city: decodeURIComponent(document.location.pathname.replace("/Weather/","").split("&")[0])}));
                if (document.getElementById('loader-overlay') !== null) document.getElementById('loader-overlay').style.display = "none";
            });
        }
        
    }
    
    render() {
     return(
        <Fragment>
            <div className="container-fluid h-100">
                <div className="row header h-60p">
                    <div className="col-4 my-auto ml-auto text-muted">
                        <h3>
                            <div className="homeButton" onClick={() => {this.props.history.push("/Weather/");}}>Home</div>
                        </h3>
                    </div>
                    <div className="col-5 my-auto mr-auto">
                        <div className="row">
                            <div className="col-6"></div>
                            <div className="col-6">
                                <SearchBox size="small"/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-9 mx-auto">
                        <div className="row">
                            <div className="col-xl-4 mr-auto">   
                                <div className="row">
                                    <div className="col-xl-6 pt-30">
                                        <Stats title="Average Temperature" data={this.state.response} value="tavg"/> 
                                    </div>
                                    <div className="col-xl-6 pt-30">
                                        <Stats title="Maximum Temperature" data={this.state.response} value="tmax"/> 
                                    </div> 
                                </div>
                                <div className="row">
                                    <div className="col-xl-6 pt-30">
                                        <Stats title="Minimum Temperature" data={this.state.response} value="tmin"/>
                                    </div>
                                    <div className="col-xl-6 pt-30">
                                        <Stats title="Global Average Deviation" data={this.state.response} value="deviation"/> 
                                    </div>
                                </div>           
                            </div>
                            <div className="col-xl-8 pt-30">
                                <div className="card rounded-0 h-100">
                                    <div className="card-body">
                                        <h5 className="card-title">{decodeURIComponent(document.location.pathname.replace("/Weather/","").split("&")[0])}</h5>
                                        <Map/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-xl-4 pt-30 mr-auto">   
                                <div className="card rounded-0">
                                    <div className="card-body">
                                        <h5 className="card-title">Yearly precipitation</h5>
                                        <div className="card-text">
                                            <Precipitation id="prcp" data={this.state.response}/>
                                        </div>
                                    </div>
                                </div>         
                            </div>
                            <div className="col-xl-8 pt-30">
                                <TemperatureWrapper data={this.state.response}/>
                            </div>
                        </div>
                    </div>
                </div>  
            </div>
        </Fragment> 
    ); 
  }
}

export default withRouter(Dashboard);