import React, {Component, Fragment } from 'react';
import ApexCharts from 'apexcharts';

class Precipitation extends Component {
    componentDidMount() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
    
    }
    
    componentDidUpdate() {
        if (Object.keys(this.props.data).length > 0) this.createGraph();
        
    }

    createGraph() {
        document.getElementById(this.props.id).innerHTML = '';

        var options = {
            series: [
                {
                    name: "Average precipitation",
                    data: [
                        this.props.data.data[0][this.props.id], 
                        this.props.data.data[1][this.props.id],
                        this.props.data.data[2][this.props.id], 
                        this.props.data.data[3][this.props.id],
                        this.props.data.data[4][this.props.id], 
                        this.props.data.data[5][this.props.id], 
                        this.props.data.data[6][this.props.id], 
                        this.props.data.data[7][this.props.id],
                        this.props.data.data[8][this.props.id], 
                        this.props.data.data[9][this.props.id], 
                        this.props.data.data[10][this.props.id], 
                        this.props.data.data[11][this.props.id]
                    ]
                }
            ],
            chart: {
                type: 'bar',
                height: 220,
                toolbar: {
                    show: false
                }
            },
            plotOptions: {
                bar: {
                    horizontal: false,
                    columnWidth: '55%',
                },
            },
            dataLabels: {
                enabled: false
            },
            stroke: {
                show: true,
                width: 2,
                colors: ['transparent']
            },
            xaxis: {
                categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
            },
            fill: {
                type: 'gradient',
                gradient: {
                    shade: 'light',
                    type: "horizontal",
                    shadeIntensity: 0.25,
                    gradientToColors: undefined,
                    inverseColors: true,
                    opacityFrom: 0.85,
                    opacityTo: 0.85,
                    stops: [50, 0, 100]
                }
            },
            tooltip: {
                y: {
                    formatter: function (val) {
                        return val + " mm"
                    }
                }
            }
        };
  
        var chart = new ApexCharts(document.getElementById(this.props.id), options);
        chart.render();
        
    }

    render() {
        return (
            <Fragment>
                <div id={this.props.id}></div>
            </Fragment>
        );
    }
}

export default Precipitation;